@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
	box-sizing: border-box;
}

html {
	font-family: 'Noto Sans JP', sans-serif;
}

body {
	margin: 0;
	background-color: #d1cfcf;
}

.hide {
	display: none;
}

.basicBack {
	position: absolute;
	width: 95%;
	height: auto;
	left: 2%;
	top: 5%;
	transition: 0.3s;
}

.light {
	background-color: rgba(173, 173, 173, 0.719);
}
.dark {
	background-color: rgba(80, 80, 80, 0.719);
}
.basicBackFlexCover {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 50px;
	margin-bottom: 70px;
	width: 98%;
	height: 98%;
	left: 1%;
	top: 1%;
}

@media only screen and (min-width: 600px) {
	.basicBack {
		width: 90%;
		left: 5%;
		top: 15%;
	}
	.basicBackFlexCover {
		width: 90%;
		height: 90%;
		top: 5%;
	}
}

@media only screen and (min-width: 1000px) {
	.basicBack {
		width: 70%;
		left: 15%;
		top: 15%;
	}
	.basicBackFlexCover {
		width: 90%;
		height: 90%;
		top: 5%;
	}
}

.itemContainer {
	position: relative;
	cursor: pointer;
	margin: 20px;
	width: 80%;
	height: 300px;
	background-color: rgba(156, 156, 156, 0.295);
	transition: 0.2s;
}
.itemContainer:hover {
	background-color: rgba(211, 207, 207, 0.753);
	-webkit-transform: scale(1.1);
	        transform: scale(1.1);
}
.itemView {
	position: relative;
	width: 90%;
	height: 55%;
	top: 10%;
	left: 5%;
}
.itemShowImage {
	position: relative;
	width: 60%;
	height: 60%;
	left: 20%;
	top: 20%;
	border-style: dashed;
	border-color: rgba(128, 128, 128, 0.055);
	border-radius: 0.2em;
}
.itemHeader {
	position: relative;
	width: 90%;
	height: 35%;
	left: 5%;
	font-weight: bold;
	font-family: Arial, Helvetica, sans-serif;
	font-size: x-large;
	text-align: center;
	padding-top: 18%;
}
.icon {
	position: relative;
	left: 25%;
	width: 50%;
	height: auto;
}
@media only screen and (min-width: 600px) {
	.itemContainer {
		left: 5%;
		margin: 20px;
		width: 40%;
		height: 300px;
	}
	.itemView {
		width: 90%;
		height: 55%;
		top: 10%;
		left: 5%;
	}
	.itemShowImage {
		width: 60%;
		height: 80%;
		left: 20%;
		top: 5%;
	}
	.itemHeader {
		width: 90%;
		height: 35%;
		left: 5%;
	}
	.icon {
		left: 0%;
		width: 100%;
		height: auto;
	}
}
@media only screen and (min-width: 1000px) {
	.itemContainer {
		margin: 20px;
		width: 20%;
		height: 300px;
	}
	.itemView {
		width: 90%;
		height: 55%;
		top: 10%;
		left: 5%;
	}
	.itemShowImage {
		width: 60%;
		height: 80%;
		left: 20%;
		top: 5%;
	}
	.itemHeader {
		width: 90%;
		height: 35%;
		left: 5%;
	}
	.icon {
		left: 0%;
		width: 100%;
		height: auto;
	}
}

.card {
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border-radius: 12px;
}

.basicBackPr {
	position: relative;
	width: 95%;
	height: auto;
	left: 2%;
	top: 5%;
	transition: 0.2s;
}
.light {
	background-color: rgba(173, 173, 173, 0.719);
}
.dark {
	background-color: rgba(80, 80, 80, 0.719);
}

.basicBackFlexCoverPr {
	position: relative;
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
	margin-top: 50px;
	margin-bottom: 70px;
	width: 98%;
	height: 98%;
	left: 1%;
	top: 1%;
}
.headNameShowPr {
	font-family: monospace;
	text-align: center;
	margin-bottom: 20px;
}
@media only screen and (min-width: 600px) {
	.basicBackPr {
		width: 80%;
		left: 10%;
		top: 15%;
	}
	.basicBackFlexCoverPr {
		width: 90%;
		height: 90%;
		left: 5%;
		top: 5%;
	}
	.headNameShowPr {
		margin-top: 90px;
	}
}

.hide {
	display: none;
}

.textName {
	position: relative;
	margin-top: 40px;
	width: 33%;
	font-size: 20px;
	font-family: monospace;
	transition: 0.2s;
}
.inputContainer {
	width: 33%;
}

.textboxShow {
	width: 80%;
	height: 40px;
	margin-top: 10px;
	margin-bottom: 10px;
	font-size: 20px;
	border-radius: 0.2em;
	font-family: monospace;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	border: none;
	transition: 0.2s;
}
.flex {
	display: flex;
	flex-wrap: wrap;
	justify-content: center;
}
.container {
	position: relative;
	width: 100%;
	left: 4%;
}

.btcontainer {
	position: relative;
	width: 50%;
	margin-top: 50px;
	margin-bottom: 20px;
	left: 40%;
}
.line {
	position: relative;
	top: 20px;
	width: 100%;
	height: 2px;
	background-color: rgb(105, 105, 105);
}
.btsubmit {
	width: 20%;
	height: 60px;
	left: 40%;
	border-radius: 0.3em;
	border: none;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	font-family: monospace;
	font-size: x-large;
	transition: 0.2s;
}
.btsubmit:hover {
	cursor: pointer;
	background-color: rgba(207, 207, 207, 0.603);
}

.mainContainerRisk {
	position: relative;
	width: 100%;
	height: auto;
	margin-top: 20px;
	margin-bottom: 20px;
	transition: 0.2s;
}

.top-cont-risks {
	width: 100%;
	height: 20px;
	transition: 0.2s;
}
.line-cont-risks {
	margin-top: 10px;
	margin-bottom: 10px;
	width: 100%;
	height: 2px;
	background-color: black;
}
.middle-cont-risks {
	width: 100%;
	transition: 0.2s;
	height: auto;
}
.bottom-cont-risks {
	width: 100%;
	transition: 0.2s;
	height: 60px;
}
.bt {
	width: 10%;
	height: 60px;
	border-radius: 0.3em;
	border: none;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	font-family: monospace;
	font-size: x-large;
	transition: 0.2s;
}
.bt:hover {
	cursor: pointer;
	background-color: rgba(207, 207, 207, 0.603);
}

.flright {
	margin-right: 20px;
	float: right;
}

.risk-container-main {
	position: relative;
	width: 100%;
	height: 90px;
	border-radius: 0.5em;
	margin-bottom: 10px;
	background-color: rgba(252, 252, 252, 0.603);
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}
.risk-flex {
	display: flex;
	flex-wrap: nowrap;
}

.risk-item-container {
	position: relative;
	height: 90px;
	border-right: 2px;
	font-size: 20px;
	font-family: monospace;
	transition: 0.2s;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

.risk-five-per {
	width: 5%;
}
.risk-ten-per {
	width: 10%;
}
.risk-fift-per {
	width: 15%;
}
.risk-twen-per {
	width: 20%;
}

.risk-item-position {
	text-align: center;
	padding-top: 32px;
}
.risk-item-position-txt {
	text-align: center;
	padding-top: 25px;
}

.risk-textbox {
	width: 80%;
	height: 40px;
	font-size: 15px;
	border-radius: 0.2em;
	font-family: monospace;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	border: none;
	transition: 0.2s;
}
.btconfirm {
	width: 60%;
	height: 40px;
	border-radius: 0.3em;
	border: none;
	box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
	font-family: monospace;
	font-size: x-large;
	transition: 0.2s;
}
.btconfirm:hover {
	cursor: pointer;
	background-color: rgba(221, 49, 26, 0.603);
}
.btconfirm:disabled:hover {
	cursor: auto;
	background-color: rgba(207, 207, 207, 0.281);
}

.btPosition {
	width: 30px;
	height: 30px;
	position: absolute;
	left: 88%;
	transition: 0.2s;
}
.pic {
	width: 100%;
	height: auto;
}

